import { CommonModule } from '@angular/common';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { StatusCount } from '../../../services/user.service';
import { IasLoaderComponent } from '../../../common/ias-loader/ias-loader.component';
import { RouterLink } from "@angular/router";
declare var bootstrap: any;

export enum AccountActivityStatus {
  APPROVED = "approved",
  CREATED = "created"
}
@Component({
  selector: 'app-dashboard',
  standalone: true,
  imports: [CommonModule, FormsModule, IasLoaderComponent, RouterLink],
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],

})

export class DashboardComponent implements OnInit {
  users: any[] = [];
  statusCount: StatusCount[] = [];   // üëà Add this line
  AccountActivityStatus = AccountActivityStatus;
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  counterUsers = 0;
  counterApproved = 0;
  counterUnapproved = 0;
  duration = 2000;
  toastMessage: string = '';
  totalUsers: number = 0;
  totalApproved: number = 0;
  totalCreated: number = 0;
  private userIdToDelete: number | null = null;
  toastType: 'success' | 'danger' | 'warning' = 'success';
  loading = false;
  pagination = {
    total: 0,
    page: 1,
    limit: 10
  };
  constructor(private http: HttpClient) { }

  @ViewChild('toastElement') toastElement!: ElementRef;

  ngOnInit(): void {
    this.loadUsers();
  }

  loadUsers(page: number = 1, limit: number = this.pageSize): void {
    this.loading = true;  // ‚¨ÖÔ∏è Start loader

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    this.http.get<any>(`${environment.apiUrl}/admin/users?page=${page}&limit=${limit}&isActive=true`, { headers })
      .subscribe({
        next: (res) => {
          this.users = res.data.data;
          this.pagination = res.data.pagination;
          this.currentPage = page;
          this.totalUsers = res.data.pagination?.total ?? this.users.length;

          this.statusCount = res.data.statusCounts ?? [];
          this.totalApproved = this.statusCount.find(s => s.status === 'approved')?.count ?? 0;
          this.totalCreated = this.statusCount.find(s => s.status === 'created')?.count ?? 0;

          this.startCounters();
          this.loading = false;  // ‚¨ÖÔ∏è Stop loader
        },
        error: (err) => {
          console.error('Error fetching users:', err);
          this.loading = false;  // ‚¨ÖÔ∏è Stop loader even on error
        }
      });
  }

  scrollToTable(): void {
    const element = document.getElementById('tableheading');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }
  startCounters() {
    this.animateCounter('counterUsers', this.totalUsers);
    this.animateCounter('counterApproved', this.totalApproved);
    this.animateCounter('counterUnapproved', this.totalCreated);
  }

  animateCounter(counterProp: 'counterUsers' | 'counterApproved' | 'counterUnapproved', target: number) {
    let start = 0;
    const increment = target / (this.duration / 16);

    const interval = setInterval(() => {
      start += increment;
      if (start >= target) {
        (this as any)[counterProp] = target;
        clearInterval(interval);
      } else {
        (this as any)[counterProp] = Math.floor(start);
      }
    }, 16);
  }

  get totalPages(): number {
    return Math.ceil(this.pagination.total / this.pagination.limit);
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.loadUsers(this.currentPage, this.pageSize); // Call API
    }
  }

  // üëà previous
  goPrevious() {
    this.changePage(this.currentPage - 1);
  }

  // üëà next
  goNext() {
    this.changePage(this.currentPage + 1);
  }


  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.loadUsers(this.currentPage, this.pageSize);
  }
  openDeleteModal(userId: number) {
    this.userIdToDelete = userId;
    const modalEl = document.getElementById('confirmDeleteModal');
    if (modalEl) {
      const modal = new bootstrap.Modal(modalEl);
      modal.show();
    }
  }

  // Called when "Deactivate" in modal is clicked
  confirmDelete() {
    if (!this.userIdToDelete) return;

    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      isActive: false,
      account: { id: this.userIdToDelete }
    };

    this.http.patch(`${environment.apiUrl}/admin/user`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast('User Account Deactivated successfully', 'success');
          this.loadUsers(); // refresh list
        },
        error: (err) => {
          this.showToast('Error Deactivating account', 'danger');
        }
      });

    // Close modal
    const modalEl = document.getElementById('confirmDeleteModal');
    if (modalEl) {
      const modal = bootstrap.Modal.getInstance(modalEl);
      modal?.hide();
    }

    this.userIdToDelete = null;
  }

  approveUser(user: any): void {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      users: [{ id: user.id }],
      status: 'approved'
    };

    this.http.put(`${environment.apiUrl}/admin/users`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast(`User ${user.doptOfficer?.englishName} approved successfully`, 'success');
          this.loadUsers();
        },
        error: (err) => {
          console.error('Error approving user:', err);
          this.showToast('Failed to approve user', 'danger');
        }
      });
  }

  rejectUser(user: any): void {
    const token = localStorage.getItem('authToken');
    const headers = new HttpHeaders({ Authorization: `Bearer ${token}` });

    const body = {
      users: [{ id: user.id }],
      status: 'created'
    };

    this.http.put(`${environment.apiUrl}/admin/users`, body, { headers })
      .subscribe({
        next: () => {
          this.showToast(`User ${user.doptOfficer?.englishName} Moved to pending list`, 'warning');
          this.loadUsers();
        },
        error: (err) => {
          console.error('Error approving user:', err);
          this.showToast('Failed to approve user', 'danger');
        }
      });
  }

  showToast(message: string, type: 'success' | 'danger' | 'warning') {
    this.toastMessage = message;
    this.toastType = type;

    const toastBootstrap = new bootstrap.Toast(this.toastElement.nativeElement);
    toastBootstrap.show();
  }

}
